import styled from "styled-components"

export const Container = styled.div`
	margin-bottom: 40px;

	a {
		color: #333;
	}
	a:hover {
		text-decoration: none;
	}

	ul {
		list-style-type: none;
		margin-top: 8px;
	}

	h5 {
		color: #13414f;
		font-family: "Roboto", sans-serif;
		font-weight: 600;
		font-size: 20px;
	}

	li {
		margin-bottom: 5px;
		font-size: 14px;
		font-family: "Roboto", sans-serif;
		display: inline;
	}

	span {
		margin-right: 8px;
	}

	img {
		width: 50%;
		border-radius: 8px;

		@media (max-width: 576px) {
			width: 100%;
		}
	}
`
