import React from "react"
import { graphql } from "gatsby"

import { ILegislation } from "../interfaces"
import Layout from "../components/Layout"
import LegislationComponent from "../components/pages/Legislation"
import SEO from "../components/utils/SEO"
import { translate } from "../components/utils/translate"
import { IBreadCrumbOption } from "../interfaces"

interface IProps {
	data: {
		allLegislationJson: {
			edges: Array<{ node: ILegislation }>
		}
	}
}

const Legislation = ({ data }: IProps) => {
	const breadCrumbList: IBreadCrumbOption[] = [
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "legislation",
			name: translate("breadCrumb.legislation"),
			isActive: false,
		},
	]

	const legislation: Array<ILegislation> = data.allLegislationJson.edges.map(
		dataNode => dataNode.node
	)
	return (
		<Layout breadCrumbList={breadCrumbList}>
			<SEO
				title="Legislações"
				description="A Kivalita Consulting tem as mais diversas soluções para o seu negócio. Encontre aqui as legislações aplicáveis de cada área, disponiveis para baixar."
				image="/images/legislation.jpeg"
			></SEO>

			<LegislationComponent legislation={legislation} />
		</Layout>
	)
}

export default Legislation

export const LegislationQuery = graphql`
	query LegislationQuery {
		allLegislationJson {
			edges {
				node {
					teste
					type
					thumbnail
					subTypes {
						slug
						thumbnailUrl
						title
						content {
							inputId
							inputName
							inputValue
							legislation
							legislationDescription
						}
					}
				}
			}
		}
	}
`
