import React from "react"

import { FaChevronRight } from "react-icons/fa"

import { Link } from "gatsby"

import "react-responsive-modal/styles.css"

import { ILegislation } from "../../../interfaces"

import { Container } from "./style"
import Banner from "../../utils/Banner"
import IntroductionContent from "../../utils/IntroductionContent"

import LinkedinImg from "../../../assets/legislation/Linkedim 520x320 (1).png"
import { translate } from "../../utils/translate"

interface IProps {
	legislation: Array<ILegislation>
}

export default function LegislationComponent(props: IProps) {
	const { legislation } = props

	return (
		<div>
			<Banner titlePage={translate('banner.legislation')} />
			<IntroductionContent
				title={translate('introduction.legislation.title')}
				subtitle={translate('introduction.legislation.subTitle')}
				content={translate('introduction.legislation.content')}
			/>
			<div className="container">
				<Container>
					<div className="row justify-content-center">
						{legislation.map(res => (
							<div
								className="col-xs-12 col-sm-6"
								key={res.type}
								style={{ marginBottom: 40, textAlign: "center" }}
							>

								<h5>{translate(`legislation.${res.type}`)}</h5>
								<div className="thumb">
									<img src={res.thumbnail} alt={res.type} />
								</div>
								<div className="container">
									<ul>
										{res.subTypes.map(sub => (
											<li key={sub.slug}>
												<span>
													<FaChevronRight color="CC3366" size="12" />
													<Link to={`/legislation/${sub.slug}`}>
														{translate(`legislation.${sub.title}`)}
													</Link>
												</span>
											</li>
										))}
									</ul>
								</div>
							</div>
						))}
						<div className="col-sm text-center">
							<img src={LinkedinImg} alt="Legislation" />
						</div>
					</div>
				</Container>
			</div>
		</div>
	)
}
